"use client";

import { Suspense, useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import Header from "@/components/Header";
import ThreeViewer from "@/components/ThreeViewer";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { getProductById } from "@/lib/products";
import { Product } from "@/lib/types";
import Link from "next/link";

interface PreviewContentProps {}

function PreviewContent({}: PreviewContentProps) {
  const searchParams = useSearchParams();
  const productId = searchParams.get("product");
  const [product, setProduct] = useState<Product | null>(null);
  const [selectedSize, setSelectedSize] = useState<string>("");
  const [rotation, setRotation] = useState(0);
  const [zoom, setZoom] = useState(100);

  useEffect(() => {
    if (productId) {
      const foundProduct = getProductById(productId);
      if (foundProduct) {
        setProduct(foundProduct);
        setSelectedSize(foundProduct.sizes[0]?.label || "");
      }
    }
  }, [productId]);

  const rotateLeft = () => {
    setRotation(prev => prev - 45);
  };

  const rotateRight = () => {
    setRotation(prev => prev + 45);
  };

  const zoomIn = () => {
    setZoom(prev => Math.min(prev + 20, 200));
  };

  const zoomOut = () => {
    setZoom(prev => Math.max(prev - 20, 50));
  };

  const centerView = () => {
    setRotation(0);
    setZoom(100);
  };

  if (!product) {
    return (
      <div className="min-h-screen bg-gray-50">
        <Header />
        <div className="flex items-center justify-center h-96">
          <p className="text-gray-500">Producto no encontrado</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {/* Header del Visualizador */}
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-6 gap-4">
          <div className="flex items-center space-x-4">
            <div className="w-12 h-12 bg-gray-200 rounded-lg flex items-center justify-center">
              <span className="text-gray-500 text-xs">IMG</span>
            </div>
            <div>
              <h1 className="font-playfair text-2xl font-bold text-black">{product.name}</h1>
              <p className="text-gray-600">Visualizador 3D - {product.material}</p>
            </div>
          </div>
          <div className="flex space-x-3">
            <Link href={`/editor?product=${product.id}`}>
              <Button variant="outline">Volver al Editor</Button>
            </Link>
            <Button className="bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white">
              Añadir al Carrito
            </Button>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
          {/* Panel Izquierdo - Modificaciones 2D */}
          <div className="lg:col-span-1">
            <Card>
              <CardHeader>
                <CardTitle className="font-playfair text-lg">Diseño y Acceso a Modificaciones 2D</CardTitle>
                <p className="text-sm text-gray-600">(El visor/editor real)</p>
              </CardHeader>
              <CardContent className="space-y-4">
                {/* Selector de Tamaño */}
                <div>
                  <h3 className="font-medium text-gray-700 mb-2">Tamaño del Producto</h3>
                  <Select value={selectedSize} onValueChange={setSelectedSize}>
                    <SelectTrigger>
                      <SelectValue placeholder="Selecciona tamaño" />
                    </SelectTrigger>
                    <SelectContent>
                      {product.sizes.map((size) => (
                        <SelectItem key={size.label} value={size.label}>
                          {size.label}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <Separator />

                <div>
                  <h3 className="font-medium text-gray-700 mb-2">Caras del Producto</h3>
                  <Button variant="outline" size="sm" className="w-full mb-2">
                    Cara A
                  </Button>
                  {product.faces === 2 && (
                    <Button variant="outline" size="sm" className="w-full">
                      Cara B
                    </Button>
                  )}
                </div>

                <Separator />

                <div>
                  <h3 className="font-medium text-gray-700 mb-2">Elementos de Diseño</h3>
                  <div className="space-y-2">
                    <div className="flex items-center justify-between p-2 bg-gray-50 rounded">
                      <span className="text-sm">twinsublime</span>
                      <Button variant="ghost" size="sm" className="text-blue-600 hover:text-blue-800">
                        Editar
                      </Button>
                    </div>
                  </div>
                </div>

                <Separator />

                <Button className="w-full bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white">
                  Modificar Diseño 2D
                </Button>
              </CardContent>
            </Card>
          </div>

          {/* Panel Central - Visualizador 3D */}
          <div className="lg:col-span-2">
            <Card>
              <CardHeader>
                <CardTitle className="font-playfair text-lg text-center">
                  Visualizador 3D - {selectedSize}
                </CardTitle>
                <p className="text-center text-sm text-gray-600">
                  Simulación realista del producto
                </p>
              </CardHeader>
              <CardContent>
                <div className="h-96">
                  <ThreeViewer 
                    product={product}
                    selectedSize={selectedSize}
                    rotation={rotation}
                    zoom={zoom}
                  />
                </div>

                <div className="mt-4 text-center">
                  <p className="text-gray-500 text-xs">
                    Rotación: {rotation}° | Zoom: {zoom}% | Tamaño: {selectedSize}
                  </p>
                </div>

                {/* Controles 3D */}
                <div className="flex justify-center items-center space-x-4 mt-6">
                  <Button variant="outline" size="sm" onClick={rotateLeft}>
                    ← Rotar Izq.
                  </Button>
                  <Button variant="outline" size="sm" onClick={centerView}>
                    Centrar
                  </Button>
                  <Button variant="outline" size="sm" onClick={rotateRight}>
                    Rotar Der. →
                  </Button>
                </div>

                <div className="flex justify-center items-center space-x-4 mt-4">
                  <Button variant="outline" size="sm" onClick={zoomOut}>
                    Zoom -
                  </Button>
                  <Button variant="outline" size="sm" onClick={zoomIn}>
                    Zoom +
                  </Button>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Panel Derecho - Información */}
          <div className="lg:col-span-1 space-y-6">
            {/* Información del Producto */}
            <Card>
              <CardHeader>
                <CardTitle className="font-playfair text-lg">Información del Producto</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <h3 className="font-medium text-gray-700">Material</h3>
                  <p className="text-sm text-gray-600">{product.material}</p>
                </div>

                <div>
                  <h3 className="font-medium text-gray-700">Tamaño Seleccionado</h3>
                  <p className="text-sm text-gray-600">{selectedSize}</p>
                </div>

                <div>
                  <h3 className="font-medium text-gray-700">Categoría</h3>
                  <p className="text-sm text-gray-600 capitalize">{product.category}</p>
                </div>

                <div>
                  <h3 className="font-medium text-gray-700">Caras</h3>
                  <p className="text-sm text-gray-600">{product.faces} Cara{product.faces > 1 ? 's' : ''}</p>
                </div>

                <div>
                  <h3 className="font-medium text-gray-700">Extras Disponibles</h3>
                  <p className="text-sm text-gray-600">{product.extras.length} opciones disponibles</p>
                </div>
              </CardContent>
            </Card>

            {/* Información del Pedido */}
            <Card>
              <CardHeader>
                <CardTitle className="font-playfair text-lg">Información del Pedido</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <h3 className="font-medium text-gray-700">Carrito</h3>
                  <p className="text-sm text-gray-600">0 productos</p>
                </div>

                <div>
                  <h3 className="font-medium text-gray-700">Precio Base</h3>
                  <p className="font-playfair text-lg font-bold text-black">
                    {product.basePrice.toFixed(2)}€
                  </p>
                </div>

                <div>
                  <h3 className="font-medium text-gray-700">Precio con Tamaño</h3>
                  <p className="text-sm text-gray-600">
                    {(() => {
                      const sizeInfo = product.sizes.find(s => s.label === selectedSize);
                      const finalPrice = product.basePrice + (sizeInfo?.priceModifier || 0);
                      return `${finalPrice.toFixed(2)}€`;
                    })()}
                  </p>
                </div>

                <Separator />

                <Button className="w-full bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white">
                  Configurar Pedido
                </Button>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
}

export default function PreviewPage() {
  return (
    <Suspense fallback={
      <div className="min-h-screen bg-gray-50">
        <Header />
        <div className="flex items-center justify-center h-96">
          <p className="text-gray-500">Cargando visualizador...</p>
        </div>
      </div>
    }>
      <PreviewContent />
    </Suspense>
  );
}
