"use client";

import { useEffect, useRef, useState } from "react";
import { Product } from "@/lib/types";

interface ThreeViewerProps {
  product: Product;
  selectedSize: string;
  rotation: number;
  zoom: number;
}

export default function ThreeViewer({ product, selectedSize, rotation, zoom }: ThreeViewerProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (!canvasRef.current) return;

    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Simular carga
    setTimeout(() => setIsLoading(false), 1000);

    // Limpiar canvas
    ctx.clearRect(0, 0, canvas.width, canvas.height);

    // Configurar canvas
    const centerX = canvas.width / 2;
    const centerY = canvas.height / 2;

    // Aplicar transformaciones
    ctx.save();
    ctx.translate(centerX, centerY);
    ctx.scale(zoom / 100, zoom / 100);
    ctx.rotate((rotation * Math.PI) / 180);

    // Dibujar producto según categoría
    drawProduct(ctx, product, selectedSize);

    ctx.restore();
  }, [product, selectedSize, rotation, zoom]);

  const drawProduct = (ctx: CanvasRenderingContext2D, product: Product, size: string) => {
    const sizeInfo = product.sizes.find(s => s.label === size);
    const width = sizeInfo ? sizeInfo.width : 20;

    // Configurar estilos base
    ctx.fillStyle = '#4F46E5'; // Color base azul
    ctx.strokeStyle = '#312E81'; // Borde más oscuro
    ctx.lineWidth = 2;

    // Gradiente para efecto 3D
    const gradient = ctx.createLinearGradient(-50, -50, 50, 50);
    gradient.addColorStop(0, '#6366F1');
    gradient.addColorStop(0.5, '#4F46E5');
    gradient.addColorStop(1, '#3730A3');

    switch (product.category) {
      case 'lanyard':
        drawLanyard(ctx, width, gradient);
        break;
      case 'pulsera':
        drawPulsera(ctx, width, gradient);
        break;
      case 'llavero':
        drawLlavero(ctx, width, gradient);
        break;
      case 'lazo':
        drawLazo(ctx, width, gradient);
        break;
      case 'cojin':
        drawCojin(ctx, gradient);
        break;
      default:
        drawGeneric(ctx, gradient);
    }

    // Añadir texto del producto
    ctx.fillStyle = '#FFFFFF';
    ctx.font = 'bold 14px Inter';
    ctx.textAlign = 'center';
    ctx.fillText('twinsublime', 0, 5);
    
    ctx.font = '10px Inter';
    ctx.fillText(size, 0, 20);
  };

  const drawLanyard = (ctx: CanvasRenderingContext2D, width: number, gradient: CanvasGradient) => {
    // Cuerpo del lanyard (largo y estrecho)
    ctx.fillStyle = gradient;
    ctx.fillRect(-width/2, -120, width, 240);
    ctx.strokeRect(-width/2, -120, width, 240);

    // Clip superior
    ctx.beginPath();
    ctx.arc(0, -130, width/2 + 5, 0, Math.PI * 2);
    ctx.fillStyle = '#374151';
    ctx.fill();
    ctx.stroke();

    // Enganche inferior
    ctx.beginPath();
    ctx.arc(0, 130, width/2 + 3, 0, Math.PI * 2);
    ctx.fillStyle = '#6B7280';
    ctx.fill();
    ctx.stroke();
  };

  const drawPulsera = (ctx: CanvasRenderingContext2D, width: number, gradient: CanvasGradient) => {
    // Forma circular de pulsera
    ctx.beginPath();
    ctx.arc(0, 0, 60, 0, Math.PI * 2);
    ctx.lineWidth = width;
    ctx.strokeStyle = gradient;
    ctx.stroke();

    // Cierre
    ctx.fillStyle = '#374151';
    ctx.fillRect(-5, -65, 10, 15);
    ctx.strokeRect(-5, -65, 10, 15);
  };

  const drawLlavero = (ctx: CanvasRenderingContext2D, width: number, gradient: CanvasGradient) => {
    // Cuerpo rectangular del llavero
    const keyWidth = width * 3;
    const keyHeight = width * 2;
    
    ctx.fillStyle = gradient;
    ctx.fillRect(-keyWidth/2, -keyHeight/2, keyWidth, keyHeight);
    ctx.strokeRect(-keyWidth/2, -keyHeight/2, keyWidth, keyHeight);

    // Argolla
    ctx.beginPath();
    ctx.arc(0, -keyHeight/2 - 15, 8, 0, Math.PI * 2);
    ctx.fillStyle = '#6B7280';
    ctx.fill();
    ctx.stroke();

    // Agujero de la argolla
    ctx.beginPath();
    ctx.arc(0, -keyHeight/2 - 15, 4, 0, Math.PI * 2);
    ctx.fillStyle = '#FFFFFF';
    ctx.fill();
  };

  const drawLazo = (ctx: CanvasRenderingContext2D, width: number, gradient: CanvasGradient) => {
    // Cinta larga y delgada
    const ribbonWidth = width * 8;
    const ribbonHeight = 8;
    
    ctx.fillStyle = gradient;
    ctx.fillRect(-ribbonWidth/2, -ribbonHeight/2, ribbonWidth, ribbonHeight);
    ctx.strokeRect(-ribbonWidth/2, -ribbonHeight/2, ribbonWidth, ribbonHeight);

    // Extremos cortados en ángulo
    ctx.beginPath();
    ctx.moveTo(-ribbonWidth/2, -ribbonHeight/2);
    ctx.lineTo(-ribbonWidth/2 + 10, 0);
    ctx.lineTo(-ribbonWidth/2, ribbonHeight/2);
    ctx.fillStyle = '#374151';
    ctx.fill();

    ctx.beginPath();
    ctx.moveTo(ribbonWidth/2, -ribbonHeight/2);
    ctx.lineTo(ribbonWidth/2 - 10, 0);
    ctx.lineTo(ribbonWidth/2, ribbonHeight/2);
    ctx.fill();
  };

  const drawCojin = (ctx: CanvasRenderingContext2D, gradient: CanvasGradient) => {
    // Cojín cuadrado con efecto 3D
    const size = 80;
    
    // Cara frontal
    ctx.fillStyle = gradient;
    ctx.fillRect(-size/2, -size/2, size, size);
    ctx.strokeRect(-size/2, -size/2, size, size);

    // Cara lateral (efecto 3D)
    ctx.fillStyle = '#3730A3';
    ctx.beginPath();
    ctx.moveTo(size/2, -size/2);
    ctx.lineTo(size/2 + 15, -size/2 - 15);
    ctx.lineTo(size/2 + 15, size/2 - 15);
    ctx.lineTo(size/2, size/2);
    ctx.closePath();
    ctx.fill();
    ctx.stroke();

    // Cara superior (efecto 3D)
    ctx.fillStyle = '#4338CA';
    ctx.beginPath();
    ctx.moveTo(-size/2, -size/2);
    ctx.lineTo(-size/2 + 15, -size/2 - 15);
    ctx.lineTo(size/2 + 15, -size/2 - 15);
    ctx.lineTo(size/2, -size/2);
    ctx.closePath();
    ctx.fill();
    ctx.stroke();

    // Costuras del cojín
    ctx.strokeStyle = '#1E1B4B';
    ctx.lineWidth = 1;
    ctx.setLineDash([5, 5]);
    ctx.strokeRect(-size/2 + 5, -size/2 + 5, size - 10, size - 10);
    ctx.setLineDash([]);
  };

  const drawGeneric = (ctx: CanvasRenderingContext2D, gradient: CanvasGradient) => {
    // Forma genérica
    ctx.fillStyle = gradient;
    ctx.fillRect(-40, -30, 80, 60);
    ctx.strokeRect(-40, -30, 80, 60);
  };

  return (
    <div className="relative w-full h-full bg-gray-50 rounded-lg border-2 border-dashed border-gray-300 flex items-center justify-center">
      {isLoading ? (
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-blue-500 border-t-transparent rounded-full animate-spin mx-auto mb-2"></div>
          <p className="text-sm text-gray-500">Cargando modelo 3D...</p>
        </div>
      ) : (
        <>
          <canvas
            ref={canvasRef}
            width={400}
            height={300}
            className="max-w-full max-h-full"
          />
          <div className="absolute top-2 right-2 bg-blue-500 text-white px-2 py-1 rounded text-xs">
            Simulación 3D
          </div>
        </>
      )}
    </div>
  );
}
